.info {
    padding: 100px;
    margin-right: 100px;
    text-align: center;
}

.intro {
    font-weight: 350;
}

.header-wrapper {
    top: 0;
    left: 0;
    width: 100%;
    position: fixed;
    background-color: #15016b;
}

p {
    font-size: 22px;
}

li {
    color: red;
}

.text-container {
    margin-bottom: 40px;
}

.skills-container {
    width: auto;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.skill-img {
    width: 5rem;
    aspect-ratio: 1/1;
    padding: 15px;
}

#res {
    height: 100%;
    width: 100%;
    margin: auto;
}

#res-block {
    padding-right: 10%;
    padding-left: 10%;
}

.small-link {
    font-size: 12pt;
}

.headshot { 
    display: inline-block;
    position: relative; 
    width: 300px; 
    height: 300px; 
    overflow: hidden; 
    border-radius: 100%;
    margin: auto;
    box-shadow: 10px 10px 10px lightgray;
    transition: 0.2s box-shadow;
 } 

.headshot img { 
    width: 100%; 
    height: auto; 
    margin-top: -25px;
}

.headshot:hover {
    box-shadow: 14px 14px 14px lightgray;
}
